@import 'normalize';
@import 'myVariables';

main {
	display: flex;
	flex-flow: column nowrap;
	width: 100%;
	height: 101%;	
	padding-left: 1em;
	padding-right: 0.5em;
	padding-bottom: 1.15em;
	margin-left: -1em;
  background: #FFFFFF url("/img/soccer-field.jpg") center/cover no-repeat;

	.welcome {
		opacity: $displayOpacity;
		display: flex;
		flex-flow: column nowrap;
		align-self: center;
		background-color: $trColorOdd;
		text-align: center;
		margin-bottom: 0;
		width: 100%;
		height: 100%;
		padding-top: 0.5em;
		padding-bottom: 0.5em;

    .welcomeText {
      text-align: center;
    }

    .leagueSelection {
      align-self: center;
    }
  }

	.club {
		display: flex;
		justify-content: center;
		align-content: center;
	}

	@media (min-width: $gtMidpoint) {
		height: 90.3vh;
		
		.welcome {
			width: 30em;
		}
	}

	.hidden {
		display: none;
	}
}


